export function createGalleryCardTemplate(data) {
  return data
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => {
        return `
    <li class="gallery-item">
      <a class="gallery-link" href="${largeImageURL}">
        <img
        width = "360px"
        // height = "240px"
          class="gallery-image"
          src="${webformatURL}"
          alt="${tags}"
        />
      </a>
      <div class="details">
        <div><p class="details-icon">‚ù§Ô∏è</p><p>${likes}</p></div>
        <div><p class="details-icon">üëÅÔ∏è</p><p>${views}</p></div>
        <div><p class="details-icon">üí¨</p><p>${comments}</p></div>
        <div><p class="details-icon">‚¨áÔ∏è</p><p>${downloads}</p></div>
      </div>
    </li>
  `;
      }
    )
    .join('');
}
